/*
 *
 * CollectionDetailPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import ConsoleCrudgridContainer from 'containers/ConsoleCrudgridContainer';
import { getSystemKey } from 'cb-utils/getPathParams';
import { withRouter, RouteComponentProps } from 'react-router';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import { Button } from 'reactstrap';
import {
  AddIcon,
  TrashIcon,
  RolesIcon,
  SettingsIcon,
  CodeIcon,
} from 'components/EntityIcons';
import PageFooter from 'components/ConsolePageFooter';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import { collections, services } from 'containers/Console/ducks';
import { CollectionModel, PrimitiveType } from 'cb-utils/console-entity-models';
import CbDropDown from 'components/CbDropDown';
import { GearDropDown } from 'containers/EdgesPage';
import { bindActionCreators } from 'redux';
import { CBDispatch } from 'cb-utils/typeUtils';
// import { FinalState } from 'react-table';
import { overallUrls } from 'cb-utils/getUrls';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

// interface CollectionDetailPageProps {}
type IRouteParams = RouteComponentProps<{ collectionName: string }>;

interface ICollectionDescription {
  collection: CollectionModel;
}

const CollectionDescription = ({ collection }: ICollectionDescription) => {
  if (collection && collection.isConnect) {
    return (
      <div>
        {collection.dbtype} {collection.dbname} / {collection.tablename} table
      </div>
    );
  } else {
    return null;
  }
};

export class CollectionDetailPage extends React.PureComponent<
  IDispatchToProps & IRouteParams & IStateToProps,
  {}
> {
  componentWillMount() {
    this.props.fetchAll(); // get all collection names and id
    this.props.fetchAllServices(); // needed for generate crud services
  }
  render() {
    const {
      collection,
      match,
      createItem,
      customColumns,
      defaultColumns,
    } = this.props;
    const collectionName = match.params.collectionName;
    const collectionID = collection && collection.collectionID;
    if (this.props.hasFetchedAll && !collection) {
      this.props.history.push(overallUrls.collections());
    }
    const title = (
      <PageHeader
        header={
          <div>
            {collectionName}{' '}
            <CbDropDown
              triggerEl={
                <GearDropDown data-cy="open-device-settings-dropdown" />
              }
              items={[
                {
                  displayName: (
                    <div>
                      <RolesIcon /> <FormattedMessage {...messages.editRoles} />
                    </div>
                  ),
                  'data-test': 'edit-collections-overall-roles-btn',
                  onClick: () =>
                    this.props.createShowBaseModal({
                      component: 'EditRolesModal',
                      props: {
                        assetClass: 'collections',
                        itemName: collectionName,
                        collectionID,
                      },
                    }),
                },
                {
                  displayName: (
                    <div>
                      <SettingsIcon />{' '}
                      <FormattedMessage {...messages.editViewDetails} />
                    </div>
                  ),
                  'data-test': 'edit-view-details',
                  onClick: () =>
                    this.props.createShowBaseModal({
                      component: 'NewCollectionModal',
                      props: { isEdit: true, collectionName },
                    }),
                },
                {
                  displayName: (
                    <div>
                      <CodeIcon />{' '}
                      <FormattedMessage {...messages.generateCrud} />
                    </div>
                  ),
                  'data-test': 'generate-crud-services',
                  onClick: () =>
                    this.props.generateCrud({
                      collectionName: this.props.collection.name,
                      schema: this.props.customColumns,
                    }),
                },
              ]}
            />
          </div>
        }
        subHeader={<CollectionDescription collection={collection} />}
        actionButton={() => (
          <Button
            onClick={() => createItem({ collectionName })}
            data-cy="add-item"
            color="primary"
          >
            <AddIcon /> <FormattedMessage {...messages.addItem} />
          </Button>
        )}
      />
    );
    return (
      <React.Fragment>
        <ConsolePage subheader={title}>
          <Helmet
            title={`ClearBlade - ${
              collection ? collectionName : 'Collection Detail'
            }`}
            meta={[
              {
                name: 'description',
                content: 'Description of CollectionDetailPage',
              },
            ]}
          />
          {collection && (
            <ConsoleCrudgridContainer
              showEditColumns={!collection.isConnect}
              allowExport
              allowSelect
              topRow={
                <Button
                  className="ml-2"
                  data-cy="delete-all-btn"
                  size="sm"
                  onClick={() => this.props.deleteAll({ collectionName })}
                >
                  <FormattedMessage {...messages.deleteAll} />
                </Button>
              }
              assetClass={'collections'}
              collectionName={collectionName}
              isLoading={!!collection}
              systemKey={getSystemKey()}
              deleteIsLoading={
                this.props.deleteIsLoading || this.props.deleteAllIsLoading
              }
              exportCallback={() =>
                this.props.createShowBaseModal({
                  component: 'ExportModal',
                  props: { collectionName, assetClass: 'collections' },
                })
              }
              createIsLoading={this.props.createIsLoading}
              collectionID={collectionID}
              columnNames={Object.keys(defaultColumns).concat(
                Object.keys(customColumns),
              )}
              updateEventCallback={(
                row: any,
                colName: string,
                val: PrimitiveType,
              ) => {
                this.props.updateItem({
                  pkName: this.props.pkName,
                  pkValue: row[this.props.pkName],
                  collectionName,
                  [colName]: val,
                });
                return;
              }}
              deleteEventCallback={(ids: string[]) => {
                this.props.deleteMultiple({
                  pkName: this.props.pkName,
                  ids,
                  collectionName,
                });
              }}
            />
          )}
        </ConsolePage>
        <PageFooter
          leftButtons={() => (
            <Button
              color="link"
              onClick={() => this.props.deleteCollection({ id: collectionID })}
              data-cy="delete-collection"
            >
              <TrashIcon />
              &nbsp; <FormattedMessage {...messages.deleteCollection} />
            </Button>
          )}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state: AppState, ownProps: IRouteParams) => {
  const collectionName = ownProps.match.params.collectionName;
  return createStructuredSelector({
    collection: collections.selectors.item(collectionName),
    hasFetchedAll: collections.selectors.fetchAll.hasFetched,
    customColumns: collections.selectors.customColumns(collectionName),
    defaultColumns: collections.selectors.defaultColumns(collectionName),
    pkName: collections.selectors.PKColumnName(collectionName),
    createIsLoading: collections.selectors.createItem.isLoading,
    deleteIsLoading: collections.selectors.deleteMultiple.isLoading,
    deleteAllIsLoading: collections.selectors.deleteAll.isLoading,
  });
};

type IStateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapDispatchToProps = (dispatch: CBDispatch) => {
  return bindActionCreators(
    {
      createShowBaseModal,
      fetchAll: collections.actions.fetchAll.request,
      updateItem: collections.actions.updateItem.request,
      createItem: collections.actions.createItem.request,
      generateCrud: services.actions.generateCrud,
      fetchAllServices: services.actions.fetchAll.request,
      deleteMultiple: (arg: {
        pkName: string;
        ids: string[];
        collectionName: string;
      }) =>
        createShowConfirmationModal(
          `Are you sure you want to delete ${arg.ids.length} rows?`,
          collections.actions.deleteMultiple.request(arg),
        ),
      deleteAll: (arg: { collectionName: string }) =>
        dispatch(
          createShowConfirmationModal(
            `Are you sure you want to all rows?`,
            collections.actions.deleteAll.request(arg),
          ),
        ),
      deleteCollection: (...args) =>
        dispatch(
          createShowConfirmationModal(
            `Are you sure you want to delete the collection?`,
            collections.actions.delete.request(...args),
          ),
        ),
    },
    dispatch,
  );
};

type IDispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(CollectionDetailPage),
);



